import React, { useState } from 'react'
import { useDispatch } from 'react-redux';
import styled from 'styled-components';
import { makeReservation, recordArrival } from '../api';
import { setRefresh } from '../slice';
import { SelectContainer } from './ModifyListpage_tableComponent'


type NotBookedInfo = {
    tableId: string;
    date: string;
    time: string;
}



const Title = styled.span``
const Header = styled.div`
    display:flex;
    flex-direction:row;
    justify-content:center;
    align-items:center;
`
const InputsContainer = styled.div`
display:flex;
flex-direction:row;
gap:30px;
`
const InputContainer = styled.div`
flex:1;
display:flex;
flex-direction:row;
justify-content:space-between;

`
const Text = styled.span`
    flex: 3;
`
const TextInput = styled.input`
    flex: 4;
`
const SelectorInput = styled.select`
    flex: 4;
`
const ButtonContainer = styled.div`
display:flex;
flex-direction:row;
gap:10px;
`
const Button = styled.button`
    flex:1;
    height:40px;
    font-size:30px;
`
const Dummy = styled.div`
    flex:1;
`


export default function NotBookedInfo({ tableId, date, time }: NotBookedInfo) {
    const [name, setName] = useState('');
    const [covers, setCovers] = useState(1);
    const [phoneNumber, setPhoneNumber] = useState('');
    const dispatch = useDispatch()

    const handleReservation = () => {
        const regPhone = /^01([0|1|6|7|8|9])-?([0-9]{3,4})-?([0-9]{4})$/;
        console.log(phoneNumber)
        if (name === '') {
            alert(' ')
            return;
        } else if (regPhone.test(phoneNumber) === false) {
            alert('  ')
            return;
        } else {
            console.log(' .')
        }
        makeReservation({
            tableId,
            date,
            time,
            name,
            covers: `${covers}`,
            phoneNumber,
        }).then(res => {
            console.log("")
            console.log(res)
        }).catch(err => {
            console.log(err)
        }).finally(() => {
            dispatch(setRefresh((value: any) => !value))
        })
    }

    const handleReservationAndArrival = () => {
        const regPhone = /^01([0|1|6|7|8|9])-?([0-9]{3,4})-?([0-9]{4})$/;
        console.log(phoneNumber)
        if (name === '') {
            alert(' ')
            return;
        } else if (regPhone.test(phoneNumber) === false) {
            alert('  ')
            return;
        } else {
            console.log(' .')
        }
        makeReservation({
            tableId,
            date,
            time,
            name,
            covers: `${covers}`,
            phoneNumber,
        }).then(res => {
            recordArrival({ table_id: tableId, date, time })
                .then(res => console.log(res))
                .catch(err => console.log(err))
        }).catch(err => {
            console.log(err)
        }).finally(() => {
            dispatch(setRefresh((value: any) => !value))
        })
    }



    return (
        <SelectContainer>
            <Header>
                {tableId}   
            </Header>
            <InputsContainer>
                <InputContainer>
                    <Text>
                        
                    </Text>
                    <TextInput
                        value={name}
                        onChange={event => {
                            const { value } = event.target;
                            setName(value)
                        }}
                    />
                </InputContainer>
                <InputContainer>
                    <Text>
                        
                    </Text>
                    <SelectorInput
                        value={covers}
                        onChange={event => {
                            const { value } = event.target;
                            setCovers(parseInt(value))
                        }}
                    >
                        <option value="1">1</option>
                        <option value="2">2</option>
                        <option value="3">3</option>
                        <option value="4">4</option>
                    </SelectorInput>
                </InputContainer>
            </InputsContainer>
            <InputsContainer>
                <InputContainer>
                    <Text>
                        
                    </Text>
                    <TextInput
                        value={phoneNumber}
                        onChange={event => {
                            const { value } = event.target;
                            setPhoneNumber(value)
                        }}
                    />
                </InputContainer>
                <Dummy />
            </InputsContainer>
            <ButtonContainer>
                <Button
                    onClick={handleReservation}
                >
                     
                </Button>
                <Button
                    onClick={handleReservationAndArrival}
                >
                     
                </Button>
            </ButtonContainer>
        </SelectContainer>
    )
}
